@use "../../../sass/custom/global/media-queries" as *;

.social-media-menu__list {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  list-style: none;
  margin: 0;
  padding: 0;

  @media ($nav) {
    justify-content: flex-end;
  }
}

.social-media-menu__link {
  position: relative;
  display: block;
  width: 24px;
  height: 24px;

  &::before {
    position: absolute;
    width: 20px;
    height: 20px;
    inset: 0;
    margin: auto;
    background-color: currentColor;
    content: "";
    mask-image: var(--icon);
    mask-repeat: no-repeat;
    mask-size: contain;
    mask-position: center;

    @media (forced-colors: active) {
      background-color: linkText;
    }
  }
}
